import { StatusBar } from 'expo-status-bar';
import { Text, View, TouchableOpacity } from 'react-native';
import { useEffect, useState } from 'react';

export default function Card({ letter, isIce, color, updateResult }) {
  const [selected, setSelected] = useState(0);

  useEffect(() => {
    if (selected !== 0) {
      if (isIce) {
        if (selected < 3) {
          // setSelected(selected + 1);
          updateResult((prev) => [...prev, letter]);
        }
      } else {
        if (selected < 2) {
          // setSelected(selected + 1);
          updateResult((prev) => [...prev, letter]);
        }
      }
    }
  }, [selected]);

  const colorCalc = () => {
    if (isIce === true) {
      if (selected < 2) {
        return '#000';
      }
      return '#fff';
    }
    if (selected < 1) {
      return color;
    }
    return '#fff';
  };

  return (
    <TouchableOpacity onPress={() => setSelected(selected + 1)}>
      <View
        style={{
          width: 40,
          height: 40,
          justifyContent: 'center',
          alignContent: 'center',
          backgroundColor: colorCalc(),
          borderStyle: isIce === true ? 'dashed' : 'none',
          borderWidth: isIce === true ? 3 : 'none',
          borderColor: isIce === true ? '#33f' : 'none',
          marginTop: 2,
          marginBottom: 2,
          borderRadius: 5,
        }}
      >
        <Text style={{ textAlign: 'center', color: '#fff' }}>{letter}</Text>
      </View>
    </TouchableOpacity>
  );
}
