import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import Card from './components/Card';
import { useEffect, useState, useRef } from 'react';
import wordlist from './assets/wordlist.json';

export default function App() {
  const renderCounter = useRef(0);
  renderCounter.current = renderCounter.current + 1;
  const lettersAndPoints = {
    A: 1,
    B: 3,
    C: 4,
    Ç: 4,
    D: 3,
    E: 1,
    F: 7,
    G: 5,
    Ğ: 8,
    H: 5,
    I: 2,
    İ: 1,
    J: 10,
    K: 1,
    L: 1,
    M: 2,
    N: 1,
    O: 2,
    Ö: 7,
    P: 5,
    R: 1,
    S: 2,
    Ş: 4,
    T: 1,
    U: 2,
    Ü: 3,
    V: 7,
    Y: 3,
    Z: 4,
  };

  const colors = [
    '#0A4D68',
    '#F97B22',
    '#6C9BCF',
    '#654E92',
    '#9BA4B5',
    '#9CA777',
    '#7C9070',
    '#088395',
    '#5F264A',
    '#957777',
    '#B0A4A4',
    '#0A4D68',
    '#F45050',
    '#394867',
    '#212A3E',
    '#263A29',
    '#41644A',
  ];

  const [point, setPoint] = useState(0);
  const [userCounter, setUserCounter] = useState(1);
  const [pointList, setPointList] = useState([]);
  const [finished, setFinished] = useState(false);
  const [result, setResult] = useState([]);
  const [inter, setInter] = useState(4000);
  const [intervalFunc, setIntervalFunc] = useState(null);
  const [isPaused, setIsPaused] = useState(false);
  const [wrongSelection, setWrongSelection] = useState(0);

  // calc states

  const [col, setCol] = useState([[], [], [], [], [], [], [], []]);

  const calcPoint = (letter) => {
    for (let i = 0; i < letter.length; i++) {
      if (lettersAndPoints[letter[i].toLocaleUpperCase('tr-TR')]) {
        setPoint((prevState) => prevState + lettersAndPoints[letter[i].toLocaleUpperCase('tr-TR')]);
      }
    }
  };

  const putLetter = async () => {
    let myInterval = setInterval(() => {
      const letter =
        Object.keys(lettersAndPoints)[
          Math.floor(Math.random() * Object.keys(lettersAndPoints).length)
        ];
      const isIce = Math.random() < 0.2;
      const color = colors[Math.floor(Math.random() * colors.length)];

      const randomNumber = Math.floor(Math.random() * 8);
      setCol((prevState) => {
        const tempCol = [...prevState];
        tempCol[randomNumber] = [{ letter, isIce, color }, ...tempCol[randomNumber]];
        return tempCol;
      });
    }, inter);
    setIntervalFunc(myInterval);
    return () => clearInterval(myInterval);
  };

  const fill8Col = () => {
    for (let i = 0; i < 8; i++) {
      const letter =
        Object.keys(lettersAndPoints)[
          Math.floor(Math.random() * Object.keys(lettersAndPoints).length)
        ];
      const isIce = Math.random() < 0.2;
      const color = colors[Math.floor(Math.random() * colors.length)];
      setCol((prevState) => {
        const tempCol = [...prevState];
        tempCol[i] = [{ letter, isIce, color }, ...tempCol[i]];
        return tempCol;
      });
    }
  };

  useEffect(() => {
    putLetter();
  }, []);

  useEffect(() => {
    if (wrongSelection === 3) {
      clearInterval(intervalFunc);
      setIntervalFunc(null);
      fill8Col();
      putLetter();
      setWrongSelection(0);
    }
  }, [wrongSelection]);

  const checkWord = (result) => {
    const word = result.join('');

    let inWordlist = wordlist.data.filter((item) => {
      return word.toLocaleLowerCase('tr-TR') === item.toLocaleLowerCase('tr-TR');
    });

    if (inWordlist.length > 0) {
      calcPoint(word);
    } else {
      setWrongSelection((prevState) => prevState + 1);
    }
  };

  useEffect(() => {
    if (finished === true && intervalFunc !== null) {
      clearInterval(intervalFunc);
      setIntervalFunc(null);
      setPointList((prevState) => [...prevState, { user: userCounter, point: point }]);
      setUserCounter((prevState) => prevState + 1);
    }
  }, [finished, intervalFunc]);

  useEffect(() => {
    if (point < 100) {
      setInter(4000);
    } else if (point < 200 && point >= 100) {
      setInter(3000);
    } else if (point < 300 && point >= 200) {
      setInter(2000);
    } else if (point < 400 && point >= 300) {
      setInter(1000);
    } else {
      setInter(1000);
    }
  }, [point]);

  useEffect(() => {
    col.forEach((item, index) => {
      if (item.length > 10 && intervalFunc !== null) {
        clearInterval(intervalFunc);
        setFinished(true);
        setIntervalFunc(null);
      }
    });
  }, [col, intervalFunc]);

  return (
    <View style={styles.container}>
      <View
        style={{
          display: finished ? 'flex' : 'none',
          flex: 5,
          justifyContent: 'center',
          alignContent: 'center',
          backgroundColor: '#000',
        }}
      >
        <Text style={{ color: '#fff', fontSize: 25, textAlign: 'center' }}>Puan : {point}</Text>
        {pointList
          .sort((a, b) => b.point - a.point)
          .map((item, index) => {
            return (
              <Text style={{ color: '#fff', fontSize: 25, textAlign: 'center' }}>
                {index + 1}. {item.user}. kullanıcı : {item.point}
              </Text>
            );
          })}
      </View>
      <View style={styles.first}>
        <Text style={{ fontSize: 50, marginRight: 20 }}>X</Text>
        <View style={styles.circle}>
          <Text style={styles.circleText}>{point}</Text>
        </View>
        <Text style={{ fontSize: 50, marginLeft: 20 }}>X</Text>
      </View>

      {/** SECOND COMPONENT */}
      <View style={styles.second}>
        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[0].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>

        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[1].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>
        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[2].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>
        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[3].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>
        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[4].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>
        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[5].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>
        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[6].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>
        <View style={{ flex: 2, justifyContent: 'space-between', marginRight: 1 }}>
          <View></View>
          <View style={{ justifyContent: 'center', alignContent: 'center' }}>
            {col[7].map((item, index) => {
              return (
                <Card
                  key={index}
                  letter={item.letter}
                  isIce={item.isIce}
                  color={item.color}
                  updateResult={setResult}
                />
              );
            })}
          </View>
        </View>
      </View>
      <View style={styles.third}>
        <TouchableOpacity
          onPress={() => {
            setResult([]);
          }}
        >
          <View style={styles.falseButton}>
            <Text style={{ fontSize: 20, color: '#fff' }}>X</Text>
          </View>
        </TouchableOpacity>
        <View
          style={{
            flex: 5,
            flexDirection: 'row',
            justifyContent: 'center',
            alignContent: 'center',
          }}
        >
          {result.map((item, index) => {
            return (
              <TouchableOpacity
                key={index}
                onPress={() => {
                  pressed(1);
                }}
              >
                <Text
                  style={{
                    textAlign: 'center',
                    color: '#fff',
                    marginLeft: 5,
                    marginRight: 5,
                    fontSize: 20,
                  }}
                >
                  {item}
                </Text>
              </TouchableOpacity>
            );
          })}
        </View>
        <TouchableOpacity
          onPress={() => {
            checkWord(result);
          }}
        >
          <View style={styles.trueButton}>
            <Text style={{ fontSize: 20, color: '#fff' }}>√</Text>
          </View>
        </TouchableOpacity>
      </View>
      <View style={styles.fourth}>
        <Text>Kelime Bulmaca - {renderCounter.current}</Text>
      </View>
      <StatusBar style="auto" />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#202020',
    alignItems: 'center',
    justifyContent: 'center',
  },
  first: {
    flex: 1,
    flexDirection: 'row',
    alignContent: 'center',
    justifyContent: 'center',
    width: '100%',
    backgroundColor: '#505050',
    alignItems: 'center',
    justifyContent: 'center',
  },

  second: {
    flex: 2,
    flexDirection: 'row',
    width: '100%',
    backgroundColor: '#202020',
  },

  // red box
  third: {
    flex: 0.25,
    flexDirection: 'row',
    width: '100%',
    backgroundColor: '#363535',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  fourth: {
    flex: 0.5,
    width: '100%',
    backgroundColor: '#505050',
    alignItems: 'center',
    justifyContent: 'center',
  },
  circle: {
    width: 100,
    height: 100,
    borderRadius: 50,
    backgroundColor: '#202020',
    textAlign: 'center',
  },
  circleText: {
    height: 100,
    textAlign: 'center',
    textAlignVertical: 'center',
    color: '#fff',
    marginTop: 30,
    fontSize: 30,
  },
  falseButton: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    width: 40,
    height: 60,
    backgroundColor: '#ff0004',
    borderRadius: 10,
  },
  trueButton: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    width: 40,
    height: 60,
    backgroundColor: '#2c6e00',
    borderRadius: 10,
  },
});
